<template>
  <div id="topnavbar">
    <nav class="navbar navbar-dark bg-dark py-0 flex-column" v-if="!showEmpty">
      <a class="navbar-brand text-center" href="#/">
        <img class="brand-image" src="@/assets/logo.png">
        <div></div>
        <span>{{projectName}}</span>
      </a>
      <div class="expand navbar-expand" id="navbarsExample04">
        <ul class="navbar-nav mr-auto flex-column">
          <li class="nav-item" v-for="item in Object.keys(topModules)" :key="item">
            <router-link class="nav-link" :to="{name: item}" @click="onModuleChanged(topModules[item])">
                <IconFont :iconName="topModuleIcons[item]"></IconFont>
                {{topModules[item]}}
            </router-link>
          </li>
        </ul>
      </div>
    </nav>
    <!-- <div style="height: 80px"></div> -->
    <div v-if="loggedIn" class="log-info">
      <span class="mr-4">{{username}}</span>
      <a v-bind:title="''" href="javascript:localStorage.removeItem('token');window.location.reload();">
        <IconFont iconName="icon-tuichu"></IconFont>
      </a>
    </div>
  </div>
</template>

<script>
import { getToken, removeToken, getUsername, getProjectInfo } from '@/utils/auth'
import utils from '@/utils/utils'
import algorithmUtils from './modules/algorithm-utils'
import apis from '@/apis/apis'
import moduleConstants from './modules/module-constants'

export default {
  name: 'TopNavBar',
  components: {
  },
  data () {
    return {
      loginModalId: utils.randomId('modalId'),
      topModules: moduleConstants.topModules,
      topModuleIcons: moduleConstants.topModuleIcons,
      showEmpty: false,
      loggedIn: getToken(),
      username: getUsername(),
      projectName: getProjectInfo().name
    }
  },
  methods: {
    getModuleName (routeBase, name) {
      return (
        routeBase + name
      )
    },
    logout () {
      removeToken()
      this.loggedIn = getToken()
    },
    loadAlgorithm () {
      let endpoint = 'algorithm/algorithms/'

      apis.apiAjaxGet(endpoint, {},
        (res) => {
          if (res.code === 0) {
            // 
            let algorithms = {}
            for (let i in res.data) {
              algorithms[res.data[i].name] = {name_ch: res.data[i].name_ch}
            }
            algorithmUtils.saveAlgorithms(algorithms)
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
    onModuleChanged(moduleName) {
      this.$emit('module-change', moduleName)
    }
  },
  computed: {
  },
  watch: {
  },
  beforeMount () {
    // console.log('beforeMount this.$route: ', this.$route)
  },
  mounted () {
    // console.log('mounted this.$route: ', this.$route)
    this.loadAlgorithm()
  }
}
</script>

<style scoped>
  .brand-image {
    width: 50px;
    height: auto;
  }
  .log-info {
    /* position: absolute; */
    bottom: 0px;
    color: white;
    text-align: center;
    width: 100%;
    margin-top: 30px;
    margin-bottom: 30px;
    /* margin-left: -15px; */
    /* padding-right: 40px; */
  }
</style>
