<template>
  <div class="container-fluid">
    <div class="row">
      <div>
        <div class="row">
          <div class="col">
            <ChannelAlgConfigPanel
              v-model="status"
              algDisplayName=""
              :modalShown="false"
              :showTitle="false"
              :showRoi="false"
              :showTimeToggle="false"
            ></ChannelAlgConfigPanel>
          </div>
        </div>
        <div class="row mt-2">
          <div class="col modal-footer">
            <button type="button" class="btn btn-primary" @click="restoreStatus"></button>
            <button type="button" class="btn btn-primary" @click="saveStatus"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ChannelAlgConfigPanel from './ChannelAlgConfigPanel'
import utils from '@/utils/utils'
import apis from '@/apis/apis'
import algorithmUtils from './algorithm-utils'

export default {
  name: 'SysConfigDefaultAlgParams',
  components: {
    ChannelAlgConfigPanel
  },
  data () {
    return {
      status: {
        currenRecordId: 0,
        configured: '1'
      }
    }
  },
  computed: {
  },
  methods: {
    loadStatus() {
      let endpoint = 'algorithm/algorithmdefaultparameters/'

      apis.apiAjaxGet(endpoint, {},
        (res) => {
          if (res.code === 0) {
            if (res.data.length > 0) {
              this.currenRecordId = res.data[0].id
              this.status = {...res.data[0]}
            } else {
              this.status = {...algorithmUtils.defaultAlgParams}
            }
            this.status.configured = '1'
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
    saveStatus() {
      let endpoint
      let request

      if (this.currenRecordId) {
        endpoint = `algorithm/algorithmdefaultparameters/${this.currenRecordId}/`
        request = apis.apiAjaxPut
      } else {
        endpoint = 'algorithm/algorithmdefaultparameters/'
        request = apis.apiAjaxPost
      }

      let data = {
        analyze_interval: this.status.analyze_interval,
        alert_interval: this.status.alert_interval,
        alert_threshold: this.status.alert_threshold,
        alert_times: this.status.alert_times
      }

      request(endpoint, data,
        (res) => {
          if (res.code === 0) {
            alert('!')
            this.loadStatus()
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
    restoreStatus () {
      if (!this.currenRecordId) {
        return
      }
      let endpoint = `algorithm/algorithmdefaultparameters/${this.currenRecordId}/`
      apis.apiAjaxDelete(endpoint, {},
        () => {
          this.currenRecordId = 0
          this.loadStatus()
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    }
  },
  watch: {
    status: {
      handler(value, oldValue) {
        console.log('status changed: ', value, oldValue)
      },
      deep: true
    }
  },
  mounted () {
    console.log('SysConfigDefaultAlgParams mounted')
    this.loadStatus()
  }
}

</script>