<template>
  <div class="container-fluid">
    <div class="row w-100 ml-1 my-2">
      <!-- <span style="display: flex; align-items: center;"><b></b></span> -->
      <button class="btn btn-light ml-4" :disabled="true"> {{usedNos.length}}/{{availNos.length}}</button>
    </div>
    <div class="row w-100 row-cols-xl-6 row-cols-md-3 row-cols-sm-3 text-center" style="width: 18rem;">
      <div class="col mb-4" v-for="item, i in Object.keys(algorithmInfo)" :key="'alg' + uniqueId + i" style="padding-bottom: 0%;">
        <div class="card border-0 h-100">
          <img src="../../assets/algo-bg.png" class="card-img"/>
          <div class="card-img-overlay">
            <div class="iconbox">
              <img :src="algorithmInfo[item].icon" class="card-img-top" :alt="algorithmInfo[item].name_ch" style="width: 50px;"/>
            </div>
            <div class="card-body">
              <p class="card-text"><b>{{algorithmInfo[item].name_ch}}</b></p>
              <p><img :src="cameraImg"/><b>{{algorithmInfo[item].count}}</b></p>
              <!-- <a href="#" class="btn btn-primary"></a> -->
              <router-link class="nav-link btn btn-primary" :to="{name: 'ModuleChConfig'}" ></router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import utils from '@/utils/utils'
import apis from '@/apis/apis'
import algorithmUtils from './algorithm-utils'

export default {
  name: 'ModuleAlgorithm',
  components: {
  },
  data () {
    return {
      uniqueId: utils.randomId('unique'),
      algorithmInfo: algorithmUtils.getAlgNameIcons(),
      cameraImg: require('../../assets/icon/camera.png'),
      freeNos: [1, 2, 3, 4, 5, 6, 7, 8],
      usedNos: [],
    }
  },
  computed: {
    availNos () {
      let nos = [...this.freeNos, ...this.usedNos]
      return nos.sort()
    }

  },
  methods: {
    getUsedFreeChannels () {
      let endpoint = 'channel/channels/get_used_free_channel_nos/'

      apis.apiAjaxGet(endpoint, {},
        (res) => {
          if (res.code === 0) {
            this.freeNos = res.data.free_nos
            this.usedNos = res.data.used_nos
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
    getAlgorithmOutline () {
      let endpoint = `channel/channelalgorithms/groupby/?by=algorithm__name&count=id`
      apis.apiAjaxGet(endpoint, {},
        (res) => {
          if (res.code === 0 && res.data) {
            res.data.forEach(v => {
              if (v.algorithm__name in this.algorithmInfo) {
                this.algorithmInfo[v.algorithm__name].count = v.count_id
              } else {
                this.algorithmInfo[v.algorithm__name].count = '0'
              }
            })
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    }
  },
  mounted () {
    this.getUsedFreeChannels()
    this.getAlgorithmOutline()
  }
}

</script>

<style>
.iconbox{
  display: flex;
  height: 100px;
  align-items: center;
  justify-content: center;
}
</style>
