export default {
  algIcons: {
    safetyhelmet: require('../../assets/icon/algorithm_icon/1.png'),
    vest: require('../../assets/icon/algorithm_icon/2.png'),
    fire: require('../../assets/icon/algorithm_icon/3.png'),
    motor: require('../../assets/icon/algorithm_icon/4.png'),
    electomotor: require('../../assets/icon/algorithm_icon/5.png'),
    cigarette: require('../../assets/icon/algorithm_icon/6.png'),
    human: require('../../assets/icon/algorithm_icon/7.png'),
    plate: require('../../assets/icon/algorithm_icon/8.png'),
    mobile: require('../../assets/icon/algorithm_icon/9.png'),
    vehicle: require('../../assets/icon/algorithm_icon/10.png'),
    animal: require('../../assets/icon/algorithm_icon/11.png'),
    facemask: require('../../assets/icon/algorithm_icon/12.png'),
  },
  defaultAlgParams: {
    analyze_interval: 1000,
    alert_interval: 30,
    alert_threshold: 0.5,
    alert_times: null,
    roi_region: null
  },
  // Save algorithms basic information get from server
  saveAlgorithms (data) {
    localStorage.setItem('algorithms', JSON.stringify(data))
  },
  getAlgoritims () {
    return JSON.parse(localStorage.getItem('algorithms'))
  },
  getAlgNameIcons() {
    let algorithms = this.getAlgoritims()
    let algInfo = {}
    for (let alg in algorithms) {
      algInfo[alg] = {name_ch: algorithms[alg].name_ch, icon: this.algIcons[alg]}
    }
    console.log('getAlgNameIcons', algInfo)
    return algInfo
  },
  initDefaultAlgStatus () {
    let status = {}
    let algorithms = this.getAlgoritims()
    Object.keys(algorithms).forEach(v=>{
      status[v] = {
        configured: '0',
        ...this.defaultAlgParams,
      }
    })
    // console.log('initDefaultAlgStatus', status)
    return status
  }
}