<template>
  <div id="roiImageBox" class="w-100 roi-preview-image">
    <img :id="roiImageId" :class="roiImgClass" :src="imgSrc" alt="">
    <canvas
      :id="canvasId"
      :width="canvasWidth"
      :height="canvasHeight"
      :class="roiCanvasClass"
      @mousedown="onMouseDown($event)"
      @mousemove="onMouseMove($event)"
      @mouseup="onMouseUp()"
      @click="onClick($event)"
      @dblclick="onDoubleClick($event)">
    </canvas>
  </div>
</template>

<script>
import utils from '@/utils/utils'

export default {
  name: 'RoiDrawPanel',
  components: {
  },
  data () {
    return {
      canvasId: utils.randomString(),
      roiImageId: utils.randomString(),
      roiImgClass: 'roiImgClassNormal',
      roiCanvasClass: 'roiCanvasClassNormal',
      canvasWidth: 1,
      canvasHeight: 1,
      roiPaths: [], // pathpath
      // 
      drawingPath: [],  // 
      mouseIsDown: false,
      mouseCurrX: 0,
      mouseCurrY: 0,
    }
  },
  props: {
    imgSrc: {
      type: String,
      required: false,
      default: '',
    },
    panelShown: {
      type: Boolean,
      required: false,
      default: false
    },
    isDrawing: {
      type: Boolean,
      required: false,
      default: false
    },
    modelValue: {
      required: true
    },
  },
  emits: ['update:modelValue'],
  computed: {
  },
  methods: {
    canvasBoxToImageBox() {
      // canvasboximage
    },
    imageBoxToCanvasBox() {
      // imageboxcanvas
    },
    imageLoaded() {
      let img=document.getElementById(this.roiImageId);
      // console.log('Image loaded', this.imgSrc, img.width, img.height, img.clientWidth, img.clientHeight, img.naturalWidth, img.naturalHeight, img.getClientRects())

      if (this.panelShown && !img.naturalWidth) {
        setTimeout(() => this.imageLoaded(), 300)
        return
      }

      this.canvasWidth = img.width
      this.canvasHeight = img.height

      // canvas
      this.loadRoiPaths(this.modelValue)

      setTimeout(() => {
        // 
        this.drawRois()
      }, 200)
    },
    drawPath(octx, path, isMoving) {
      // 
      if (path.length === 0) {
        return
      }
      octx.save()
      if (isMoving) {
        octx.strokeStyle = 'rgba(30, 200, 30, 1)'
        octx.fillStyle = 'rgba(30, 200, 30, 1)'
        octx.fillRect(path[0][0]-1, path[0][1]-1, 3, 3)
      } else {
        octx.strokeStyle = 'rgba(30, 30, 200, 1)'
      }
      octx.beginPath()
      octx.lineWidth = 2
      octx.moveTo(path[0][0], path[0][1])
      path.slice(1).forEach(v=>octx.lineTo(v[0], v[1]))
      octx.closePath()
      octx.stroke()
      octx.restore()
    },
    drawRois() {
      let canvas = document.getElementById(this.canvasId)
      // Create an offscreen canvas
      let oc = document.createElement('canvas')
      oc.width = canvas.width
      oc.height = canvas.height
      let octx = oc.getContext('2d')

      this.roiPaths.forEach(v=>{
        this.drawPath(octx, v, false)
      })

      // 
      let path
      if (this.mouseIsDown) {
        path = [...this.drawingPath, [this.mouseCurrX, this.mouseCurrY]]
      } else {
        path = this.drawingPath
      }
      if (path.length) {
        this.drawPath(octx, path, true)
      }

      // Then copy from offscreen to on screen
      let imgData = octx.getImageData(0, 0, oc.width, oc.height)
      let ctx = canvas.getContext('2d')
      ctx.putImageData(imgData, 0, 0)
    },
    filterRoiPaths() {
      // 
      // let filteredResults = []
      // for (let i in this.roiPaths) {
      //   let v1 = this.roiPaths[i]
      //   let discard = false
      //   for (let j in this.roiPaths) {
      //     if (i !== j) {
      //       let v2 = this.roiPaths[j]
      //       // v1v2
      //       if (v1[0] >= v2[0] && v1[1] >= v2[1] && v1[2] <= v2[2] && v1[3] <= v2[3]) {
      //         discard = true
      //         break
      //       }
      //     }
      //   }
      //   if (!discard) {
      //     filteredResults.push(v1)
      //   }
      // }
      // this.roiPaths = filteredResults
    },
    roiPathsToEvent() {

      if (!this.roiPaths.length) {
        return null
      }

      // roiPaths: canvas->image
      let img=document.getElementById(this.roiImageId);

      let ratioX = img.naturalWidth / this.canvasWidth
      let ratioY = img.naturalHeight / this.canvasHeight
      let resultPaths = this.roiPaths.map(v => v.map(v1 => [Math.round(v1[0] * ratioX), Math.round(v1[1] * ratioY)]))

      console.log('roiPathsToEvent', resultPaths)

      return JSON.stringify(resultPaths)
    },
    resetRois () {
      this.roiPaths = []
      this.drawingPath = []
    },
    loadRoiPaths(modelValue) {
      if (!modelValue) {
        this.resetRois()
        return
      }
      // roiPathsToEvent
      let img=document.getElementById(this.roiImageId);

      try {

        let ratioX = this.canvasWidth / img.naturalWidth
        let ratioY = this.canvasHeight / img.naturalHeight

        let roiPaths = JSON.parse(modelValue)

        this.roiPaths = roiPaths.map(v => v.map(v1 => [Math.round(v1[0] * ratioX), Math.round(v1[1] * ratioY)]))
      } catch(e) {
        console.log('loadRoiPaths exception', e)
      }

      console.log('loadRoiPaths', this.roiPaths)
    },
    onMouseDown($event) {
      console.log('onMouseDown', $event)
      if (this.isDrawing && $event.buttons == 1) {
        this.mouseIsDown = true
        this.mouseCurrX = $event.offsetX
        this.mouseCurrY = $event.offsetY
        this.drawRois()
      }
    },
    onMouseMove($event) {
      // console.log('onMouseMove', $event)
      // 
      if (this.isDrawing && $event.buttons == 1 && this.mouseIsDown && this.drawingPath.length) {
        // this.drawingPath[this.drawingPath.length-1] = [$event.offsetX, $event.offsetY]
        this.mouseCurrX = $event.offsetX
        this.mouseCurrY = $event.offsetY
        this.drawRois()
      }
    },
    onMouseUp($event) {
      console.log('onMouseUp', $event)
      if (this.isDrawing && this.mouseIsDown) {
        this.mouseIsDown = false
        this.drawingPath.push([this.mouseCurrX, this.mouseCurrY])
      }
    },
    onClick($event) {
      console.log('onClick', $event)
    },
    onDoubleClick($event) {
      console.log('onDoubleClick', $event)
    },
  },
  watch: {
    isDrawing () {
      // pathroiPaths
      if (!this.isDrawing && this.drawingPath.length >= 3) {
        this.roiPaths.push(this.drawingPath)
        this.$emit('update:modelValue', this.roiPathsToEvent())
      }
      this.drawingPath = []
      this.drawRois()
    },
    panelShown () {
      if (this.panelShown) {
        // canvas
        setTimeout(() => this.imageLoaded(), 500)
      }
    },
    modelValue() {
      console.log('roi modelValue changed', this.modelValue)
      // 
      this.loadRoiPaths(this.modelValue)
      this.drawRois()
    }
  },
  mounted () {
  }
}
</script>

<style>
.roi-preview-image {
  height: 400px;
  max-width: 100%;
  display: relative;
}
.roiCanvasClassNormal {
  display: '';
  position: absolute;
}
/* .roiCanvasClassHidden {
  display: none;
} */
.roiImgClassNormal {
  max-width: 100%;
  max-height: 100%;
  position: absolute;
}
/* .roiImgClassHidden {
  display: none;
} */
</style>