<template>
  <div class="container-fluid" style="height: 100vh">
    <div class="row " style="height: 100vh">
      <div class="col-xl-2 col-lg-2 col-md-3 col-sm-3 bg-dark">
        <TopNavBar @module-change="onModuleChanged"></TopNavBar>
      </div>
      <div class="col">
        <div class="row mt-2 mb-2 mr-2 border-bottom">
          <div class="col">
            <span class="top-module-caption">{{currModuleName}}</span>
          </div>
          <div class="col text-right" style="height: 32px;">
            {{dateString}}
          </div>
        </div>
        <div class="row">
          <router-view></router-view>
        </div>
      </div>
    </div>
    <ActivateDlg
      :modalId="authModalId"
      @auth-success="authSuccess"
    ></ActivateDlg>
    <LoginDlg
      :modalId="loginModalId"
      :title="projectName || ''"
      @login-success="loginSuccess"
    ></LoginDlg>
  </div>
</template>

<script>
import ModuleSysConfig from '@/components/modules/ModuleSysConfig'
import ModuleDashboard from '@/components/modules/ModuleDashboard'
import ModuleChConfig from '@/components/modules/ModuleChConfig'
import ModuleAlertConfig from '@/components/modules/ModuleAlertConfig'
import ModuleAlerts from '@/components/modules/ModuleAlerts'
import ModuleAlgorithm from './modules/ModuleAlgorithm'
import TopNavBar from './TopNavBar.vue'
import ActivateDlg from './modules/ActivateDlg'
import LoginDlg from './modules/LoginDlg'
import { getToken, removeToken, setProjectInfo, getProjectInfo } from '@/utils/auth'

import dayjs from 'dayjs'
import utils from '@/utils/utils'
import $ from 'jquery'
import apis from '@/apis/apis'
import moduleConstants from './modules/module-constants'

export default {
  name: 'MainPanel',
  children: [
    {path: 'ModuleSysConfig', name: 'ModuleSysConfig', component: ModuleSysConfig, children: ModuleSysConfig.children, redirect: '/ModuleSysConfig/SysConfigDefaultAlgParams'},
    {path: 'ModuleChConfig', name: 'ModuleChConfig', component: ModuleChConfig},
    {path: 'ModuleAlertConfig', name: 'ModuleAlertConfig', component: ModuleAlertConfig},
    {path: 'ModuleAlerts', name: 'ModuleAlerts', component: ModuleAlerts},
    {path: 'ModuleDashboard', name: 'ModuleDashboard', component: ModuleDashboard},
    {path: 'ModuleAlgorithm', name: 'ModuleAlgorithm', component: ModuleAlgorithm}
  ],
  components: {
    TopNavBar,
    ActivateDlg,
    LoginDlg
},
  data () {
    return {
      authModalId: utils.randomId('modalId'),
      loginModalId: utils.randomId('loginModalId'),
      dateString: '',
      loggedIn: getToken(),
      currModuleName: ''
    }
  },
  computed: {
    projectName () {
      let pi = getProjectInfo()
      if (pi) {
        return pi.name
      }
      return null
    }
  },
  methods: {
    authSuccess () {
      let endpoint = 'system/projectinfos/auth_info/'
      apis.apiAjaxGet(endpoint, {},
        (res) => {
          // console.log(endpoint, res)
          if (res.code === 0 && res.data) {
            setProjectInfo(res.data)
            $(`#${this.loginModalId}`).modal('show')
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
    loginSuccess () {
      this.loggedIn = getToken()
      window.location.reload()
    },
    onModuleChanged (moduleName) {
      this.currModuleName = moduleName
    }
  },
  mounted() {
    // URL
    if (this.$route.fullPath.split('/').length >= 2) {
      let topPathName = this.$route.fullPath.split('/')[1]
      if (topPathName in moduleConstants.topModules) {
        this.currModuleName = moduleConstants.topModules[topPathName]
      }
    }
    setInterval(() => {
      this.dateString = dayjs().format('YYYYMMDD HH:mm:ss')
    }, 1000)
    setTimeout(() => {
      let endpoint = 'system/projectinfos/auth_info/'
      apis.apiAjaxGet(endpoint, {},
        (res) => {
          // console.log(endpoint, res)
          if (res.code === 0) {
            if (!res.data) {
              removeToken()
              $(`#${this.authModalId}`).modal('show')
            } else {
              setProjectInfo(res.data)
              if (!this.loggedIn) {
                $(`#${this.loginModalId}`).modal('show')
              }
            }
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    }, 200)
  }
}

</script>

<style>
  .top-module-caption {
    font-size: 1.5em;
  }
</style>